.app {
  margin: 2em;
}

.panel-attribute {
  margin-top: 0.5em;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 10;
  -webkit-box-orient: vertical;

}

.panel-class {
  font-size: 1em;
  font-weight: 200;
}

.panel-title > a{
  font-size: 1em;
  font-weight: 500;
  color: #212529;
  text-decoration: none !important;
}

.panel-title > a:hover {
  color: #205eC8;
}

.panel-object {
  overflow: visible;
  display: inline !important;
  text-decoration: none;
}

.panel-object:hover {
  text-decoration: underline;
}

.panel-summary {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 10;
  -webkit-box-orient: vertical;
}

.main {
  max-width: 1200px;
  margin: 0 auto;
}

/* li > * {
  vertical-align: text-top;
}

.list-group-item {
  padding: 1em !important;
} */

.result-class {
  font-size: 1em;
  font-weight: 200;
}

.result-summary {
  margin: 1em 0;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

.result-link {
  font-size: 1em;
  text-decoration: none;
}

.result-link:hover {
  text-decoration: underline;
}

.result-title {
  cursor: pointer;
  font-size: 1.5em;
  font-weight: 500;
  color: #212529;
  text-decoration: none;
}

.result-title:hover {
  color: #205eC8;
}

.error {
  visibility: hidden;
  text-align: center;
}

@media screen and (max-width: 800px) {
  .main {
    display: None;
  }

  .error {
    visibility: visible; 
  }
}
